main {
  -webkit-transition: padding 250ms;
  -o-transition: padding 250ms;
  transition: padding 250ms;
  position: relative; }
  main h1, main h2, main h3, main h4, main h5, main h6 {
    margin-top: 0; }
  main p {
    line-height: 1.25; }
  @media (min-width: 700px) {
    main h1, main h2, main h3, main h4, main h5, main h6 {
      margin-top: 1rem; } }



