@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,700|Nunito:400,700");
*, *::before, *::after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

a, a:hover, a:active, a:visited {
  text-decoration: none;
  color: var(--ocean);
  -webkit-transition: color 250ms;
  -o-transition: color 250ms;
  transition: color 250ms; }
  a.text-link, a:hover.text-link, a:active.text-link, a:visited.text-link {
    margin-left: 5px;
    padding: 1px 2px;
    background-color: var(--color-powder);
    border-bottom: 1px solid var(--color-helium); }

a:hover {
  color: var(--color-helium); }

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-heading); }

p {
  font-family: var(--font-paragraph);
  line-height: 1.2; }

body {
  font-size: 12pt;
  font-family: var(--font-paragraph);
  letter-spacing: 1px;
  color: var(--dark);
  width: 100vw;
  max-width: 100vw;
  margin: 0 auto;
  background-color: var(--color-powder);
  position: relative;
  -webkit-transition: max-width 250ms, border-radius 250ms;
  -o-transition: max-width 250ms, border-radius 250ms;
  transition: max-width 250ms, border-radius 250ms;
  overflow-x: hidden;
  background-image: -webkit-gradient(linear, left top, left bottom, from(var(--color-pearl)), to(var(--color-tin)));
  background-image: -webkit-linear-gradient(top, var(--color-pearl), var(--color-tin));
  background-image: -o-linear-gradient(top, var(--color-pearl), var(--color-tin));
  background-image: linear-gradient(to bottom, var(--color-pearl), var(--color-tin)); }

main {
  -ms-flex: 1 1;
  flex: 1 1;
  background-color: #fff; }

#root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  min-height: 100vh; }

@media (min-width: 1120px) {
  body {
    margin: 0 auto;
    max-width: 1080px; }
  main {
    -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden; } }

.horizontalRule {
  margin: 2rem 0;
  border-bottom: 2px solid var(--color-pearl); }

ul.demos {
  padding: 0;
  list-style-type: none; }



