.content-block {
  margin: 0;
  padding: 2rem 1rem;
  -webkit-transition: margin 250ms;
  -o-transition: margin 250ms;
  transition: margin 250ms; }
  @media (min-width: 700px) {
    .content-block {
      padding: 3rem; } }
  @media (min-width: 500px) {
    .content-block.compact {
      padding: 2rem; } }



